.header{
  width:100%;
  height:80px;
  background-color:#D32E2E;
  display: flex;
  justify-content: center;
}

.head-container{
  width:80%;
  display: flex;
}

.header-left{
  width:30%;
  display:flex;
}

.header-right{
  width:70%;
  padding-right:20px;
  display:flex;
  flex-direction: row;
  justify-content: flex-end;
}

.user-section{
  width:30%;
  height:100%;
  float:right;
  display:flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}
